<template>
  <div class="singers">
    <canvas id="canvas1" width="400" height="400"></canvas>
    <div>
      <span>{{content}}</span>
    </div>
  </div>
</template>

<script>
import { setInterval } from "timers";
export default {
  data() {
    return {
      content: 3
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      setTimeout(() => {
        var canvas = document.getElementById("canvas1");
        var ctx = canvas.getContext("2d");
        //绘制图层清空；
        ctx.clearRect(0, 0, 400, 400);
        //动态文字
        ctx.font = "30px SimHei";
        ctx.fillText("3秒后跳回首页", 90, 210);
        //开始先画一个○；
        ctx.beginPath();
        //描边的宽度；
        ctx.lineWidth = 10;
        // 修改样式
        ctx.strokeStyle = "#0f0";
        //画一个圆
        ctx.arc(200, 200, 180, 0, (360 * Math.PI) / 180);
        // 描边
        ctx.stroke();
        // 设置定时，跳回首页；
        setTimeout(() => {
          history.go(-1);
        }, 3000);
      }, 300);
    }
  },
};
</script>

<style>
.singers {
  background-color: #fff;
}
</style>
